export default {
    name: "app-diy-composition-image",
    props: {
        imageList: Array,
        mode: String,
    },
    computed: {
        imageClass() {
            let count = this.imageList.length;
            switch (count) {
                case 1:
                    return 'goods-one';
                case 2:
                    return 'goods-two';
                case 3:
                    return 'goods-three';
                case 4:
                    return 'goods-four';
                case 5:
                    return 'goods-five';
            }
        },
    },
}
